body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
	box-sizing: border-box;
}
.Box {
	width: calc(25% - 40px);
	height: 100px;
	background-color: red;
	margin: 20px;
	float: left;
	box-sizing: border-box;
	text-align: center;
}

.boxText {
	line-height: 60px;
}

@media screen and (max-width: 900px) {
	.Box {
		width: calc(50% - 40px);
	}
}
.BoxButton {
	width: 50%;
	height: 40px;
	box-sizing: border-box;
	cursor: pointer;
}
.Header {
	width: 100%;
	height: 50px;
	background-color: blue;
}

.HeaderButton {
	width: 100px;
	height: 30px;
	margin: 10px 20px;
	box-sizing: border-box;
	float: left;
	background-color: green;
	cursor: pointer;
}
.AdminContent {
	width: 900px;
	margin: auto;
}

.AdminTable {
	width: 100%;
	margin-top: 50px;
	/*border: 1px solid black;*/
	border-collapse: collapse;
}

th {
	text-align: left;
	height: 50px;
	background-color: #eae;
	padding: 5px;
}

.AdminAddRowInput {
	width: 100%;
	height: 50px;
	font-size: 16px;
}
.editCell {
	text-align: center;
	border: 1px solid red;
	cursor: pointer;
	line-height: 30px;
	width: calc(50% - 20px);
	margin: 10px;
	
}
.editCell:hover {
	background-color: red;
}

td {
	line-height: 50px;
	width: 33%;
}

tr:hover {
	background-color: #eee;
}

tr {
	border-bottom: 1px solid #ddd;
}

.AdminAddRow {
	width: 100%;
	height: 80px;
	text-align: center;
	line-height: 80px;
}

.AdminAddButton {
	text-align: center;
	border: 1px solid red;
	cursor: pointer;
	line-height: 30px;
	width: 200px;
	margin: 10px;
	font-size: 20px;
}

.AdminAddButton:hover {
	background-color: red;
}
